@import './variable.scss';

.search__wrapper{
    position: relative;
    width: 850px;
    margin: 0 auto;
}
.search{
    margin: 0 auto;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__btn{
        @include btn;
        height: 50px;
        border: 1px solid rgb(80, 77, 77);
    }
    &__btn:hover{
        background-color: rgb(80, 77, 77);
    }
}
input{
    display: block;
    width: 700px;
    height: 50px;
    padding: 10px;
    margin: 0 auto;
    outline: none;
    border-radius: 4px;
    border: none;
    background-color: rgb(80, 77, 77);
    color: rgb(11, 155, 107);
    text-shadow: 2px 2px 4px black;
}
.radio__btn{
    position: absolute;
    top: 60px;
    left: 50px;
    width: 250px;
    height: 30px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
}
.radio__btn label{
    width: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 25px;
    color: rgb(11, 155, 107);
    text-shadow: 2px 2px 4px black;
    cursor: pointer;
}
.radio__btn input{
    margin: 0;
    width: 15px;
    margin-right: 10px;
    cursor: pointer;
}

