.card{
    width: 250px;
    height: 400px;
    border: 1px solid rgb(11, 155, 107);
    &__img{
        width: 100%;
        height: 300px;
    } 
    &__img img{
        width: 100%;
        height: 100%;
    }   
    &__descr{
        padding: 10px;
        width: 100%;
        height: 100px;
        background-color: rgb(11, 155, 107);
    }
    &__descr h4{
        text-align: center;
        font-weight: bold;
        font-size: 18px;
    }
    &__descr p{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}