@import './variable.scss';
header{
    width: 100%;
    height: 70px;
    background-color: rgb(80, 77, 77);
}
nav{
    width: 100%;
    height: 100%;
}
.menu{
    height: 100%;
    display: flex;
    align-items: center;
    &__item{
        list-style-type: none;
        color: rgb(11, 155, 107);
        text-transform: uppercase;
        text-shadow: 2px 2px 4px black
    }
    &__link{
        @include btn;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__item:nth-child(2){
        margin-left: 20px;
    }
    &__item:nth-child(3){
        margin-left: 1000px;
    }
}
